import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };

  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts.mx/api/v2/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
  };
  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(
              ({ title, id, year, summary, medium_cover_image, genres }) => (
                <Movie
                  title={title}
                  key={id}
                  id={id}
                  year={year}
                  summary={summary}
                  poster={medium_cover_image}
                  genres={genres}
                />
              )
            )}
          </div>
        )}
      </section>
    );
  }
}
/*
  리액트는 자동적으로 주어진 props들이 있음
  ex) onClick 자동적으로 eventListener까지 등록되어 있음
*/
export default App;
